.PortfolioPage{

}

.portfolis{
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 2rem;
  column-gap: 1rem;
  @media screen and (max-width: 1600px){
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 900px){
    grid-template-columns: repeat(1, 1fr);
  }
  .portfolio{
    .image-data{
      position: relative;
      &::before{
        content: "";
        position: absolute;
        top: 12px;
        left: 15px;
        height: calc(100% - 30px);
        width: calc(100% - 30px);
        background-color: rgba(255, 255, 255, 0.57);
        transform-origin: center;
        transform: scale(0);
        transition: all .4s ease-in-out;

      }
      &:hover::before{
        transform: scale(1);
      }
      img{
        width: 100%;
        height: 30vh;
        object-fit: cover;
      }

      .hover-items{
        list-style: none;
        position: absolute;
        top: 70%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 1rem 2rem;
        visibility: hidden;
        li{
          a{
            padding: 1rem;
            text-decoration: none;
            font-family: inherit;
            border-radius: 10px;
            background-color: rgb(129, 105, 173);
            color: rgb(255, 255, 255);
            &:not(:last-child){
              margin-right: 2rem;
            }
          }
        }
      }

      &:hover{
        .hover-items{
          transition: all .5s ease-in-out .2s;
          visibility: visible;
          top: 50%;
        }
      }
    }

    h5{
      font-weight: 400;
      color: white;
      transition: all .6s ease-in-out;
      font-size: 1.40rem;
      line-height: 1.75rem;
      font-family: 'Nunito,sans-serif';
      &:hover{
        color: #313131;
        cursor: pointer;
        padding: 25px;

      }

    }
  }
}